		Loadthepage(e) {
			var self = this;
			if (!self.loadshow) {
				return
			}
			if (self.page < self.pages) {
				var page = self.page + 1;
				self.$service.auth(self, self.$service.api.main.merch_list, "POST", {
					page: page,
					limit: 15,
					id: '',
					type: e,
					lat: self.longitude,
					lng: self.latitude,
					rate: 1,
					distance: 1
				}, function(self, res) {
					console.log(res)
					self.pages = res.data.page_total;
					self.page = page;
					// self.activity = res.data.activity
					var list = res.data.data;
					list.forEach((v, i) => {
						self.sercontlist.push(v);
					});
					if (self.pages == self.page || self.sercontlist.length == 0) {
						console.log("false");
						self.loadshow = false
						uni.showToast({
							title: '数据已加载完毕',
							icon: 'none'
						});
					} else {
						self.loadshow = true
					}
				});
			} else {
				self.loadshow = false
				uni.showToast({
					title: '数据已加载完毕',
					icon: 'none'
				});
			}
		},

